// prettier-ignore
export default class Waves extends React.Component<IWavesProps, IWavesState> {
  state: IWavesState = {
    count: 0,
    clicked: false,
  };

  public render() {
    return (
      <Box
        flex={1}
        sx={{
          display: "flex",
          alignItems: "stretch",
          position: "absolute",
          height: "100vh",
          width: "100%",
          backgroundSize: 'cover'
        }}
      >

        {/* 
        //@ts-ignore */}
        <MorphReplace width="100%" height="100%" preserveAspectRatio="none" rotation="none" duration={1750} easing={easeInQuad}> 
          {this.state.clicked ? <BG1 key="bg1" /> : <BG2 key="bg2" />}
        </MorphReplace>
      </Box>
    );
  }
  
  componentDidMount() {
    setInterval(() => {
      this.setState((state) => ({
        clicked: !state.clicked,
      }));
      // this.forceUpdate();
    }, 1750);
  }
  
}
